import React, { Component } from 'react';
import Link from 'next/link';

class CtaAreaTwo extends Component {
    render() {
        return (
            <div className="cta-area">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-lg-8 col-md-8">
                            <div className="cta-content">
                                <h3>Tìm hiểu về bất động sản <b>tốt nhất</b> tại Lâm Đồng</h3>
                                <span>Đất nên, nhà ở, đất nông nghiệp, đất nền dự án.</span>
                            </div>
                        </div>

                        <div className="col-lg-4 col-md-4 text-right">
                            <Link href="#contact">
                                <a className="btn btn-primary">Liên Hệ</a>
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default CtaAreaTwo;